import React, { useEffect,useRef,useState } from 'react';
import  makeStyles  from '@material-ui/core/styles/makeStyles';
import { ThemeProvider as MuiThemeProvider } from '@material-ui/core/styles';
import  Grid  from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import Badge from '@material-ui/core/Badge';
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary';
import { CachedRounded, Camera, FontDownloadRounded, GetAppRounded, InsertChartRounded, RefreshRounded, Timer } from '@material-ui/icons';
import { Fab } from '@material-ui/core';
import  clickSoundfile from  './camera-shutter-click.mp3'; 
import { Decoder } from '@nuintun/qrcode';
const clickSound = new Audio(clickSoundfile);
const useStyles = makeStyles((theme) => ({
  dialogBody:{
    textAlign:'center'
  },
  videoBarContainer:{
    position: 'absolute',
    top: 0,
    bottom: 0,
    width: '100%',
    height: '100%', 
    overflow: 'hidden'
  },
  videoBar:{
    minWidth: '100%',
    minHeight: '100%',
    width:"auto",
    height:'auto',
    position:'absolute',
    top:'50%',
    left:"50%",
    transform:`translate(-50%,-50%) rotateY(180deg)`,
  },
  bottomIconPallate:{
    position:'absolute',
    bottom:'20px',
    left:'39%'
  },
  canvas:{
    display:'none'
  },
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  bottomIconLeftPallate:{
    position:"absolute",
    bottom:'20px',
    right:'8%'
  },
  previewBOx:{
    overflow:'hidden'
  },
  imagePreview:{
    maxWidth:'100%',
    maxHeight:'100%'
  },
  listingBox:{
    width:'auto',
    height:'100vh',
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
  countdown:{
    position:"absolute",
    bottom:'50%',
    right:'50%'
  },
  heading:{
    position:'fixed',
    top:'50%',
  }
}));
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});
const times =  [3,5,10];
function App() {
  const classes  = useStyles();
  const [support,setSupport] = useState(false);
  const [faceMode,setFaceMode] = useState('user');
  const [isError,setIsError] = useState(0);
  var [track,setTrack] = useState({});
  var  canvas =  useRef(null);
  const player  = useRef({});
  const [open, setOpen] = useState(false);
  const [pictureArr,setPictureArr] = useState([]);
  const [selectedImage,setSelectedImage] = useState(null);
  const [currentTimer,setCurrentTimer] = useState(null);
  const captureImage = ()=>{
    if(currentTimer != null){
      var countdown =currentTimer;
      var inter = setInterval(() => {
        console.log(currentTimer);
        if(countdown <= 0){
        new ImageCapture(track).takePhoto()
        .then(blob => createImageBitmap(blob))
        .then(imageBitmap => {
          var width = 1000;
          var context = canvas.current.getContext('2d');
            canvas.current.width = width;
            canvas.current.height = width;
            context.drawImage(imageBitmap, 0, 0, width, width);
            setPictureArr(old=>[...old,canvas.current.toDataURL('image/png')])
          });
          setCurrentTimer(null);
          clickSound.play();
          clearInterval(inter);
        }
        setCurrentTimer(countdown);
        --countdown;
      },1000);
    }
    else {
      new ImageCapture(track).takePhoto()
      .then(blob => createImageBitmap(blob))
      .then(imageBitmap => {
        var width = 1000;
        var context = canvas.current.getContext('2d');
        context.translate(width,0);
        context.scale(-1,1);
          canvas.current.width = width;
          canvas.current.height = width;
          context.drawImage(imageBitmap, 0, 0, width, width);
          setPictureArr(old=>[...old,canvas.current.toDataURL('image/png')])
          const qrcode = new Decoder();
          // set options
          qrcode.setOptions({ 
            inversionAttempts: 'dontInvert' | 'onlyInvert' | 'attemptBoth' | 'invertFirst' 
          });
          qrcode
            .scan('data:image/png;base64,')
            .then(result => {
              console.log(result.data);
            })
            .catch(error => {
              console.error(error);
            });
        });
        clickSound.play();
    }
  }
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const forcePreview = (index)=>{
    setSelectedImage(pictureArr[index]);
  }
  const forceDownload = (index) =>{
    var a = document.createElement("a"); //Create <a>
    a.href = pictureArr[index]; //Image Base64 Goes here
    a.download = Math.random()*100+'.png'; //File name Here
    a.click();
  }
  const setupTimer = ()=>{
    var index = times.indexOf(currentTimer);
    if(index == -1){
        setCurrentTimer(times[0]);
    }
    else if(index == times.length - 1){
      setCurrentTimer(null);
    }
    else if(index >= 0 && index < times.length - 1){
      setCurrentTimer(times[index+1]);
    }
     else {
       setCurrentTimer(times[0]);
     }
  }
  const handlefaceMode = ()=>{
    if(faceMode == "user"){
      setFaceMode('environment');
    }
    else{
      setFaceMode('user');
    }
  }
  useEffect(()=>{
    var pla  =  player.current;
    setSupport('mediaDevices' in navigator);
    window.onfocus = ()=>{
      //pla.srcObject.getVideoTracks().forEach(track => track.stop());
    }
    window.onblur = ()=>{
       //pla.srcObject.getVideoTracks().forEach(track => track.start());
    }
    navigator.mediaDevices.getUserMedia({
      video: {
        aspectRatio: { ideal: 1.7777777778 },
        facingMode:faceMode
    } 
    }).then((stream)=>{
      pla.srcObject = stream;
      setTrack(stream.getVideoTracks()[0]);
    }).catch(err=>{ 
      setIsError(true);
    });
  },[]);
  return (
    isError ? <Typography variant="h1" className={classes.heading} >error while connecting camera</Typography>:
    <div>
       <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Pictures
            </Typography>
          </Toolbar>
        </AppBar>
        <div className={classes.dialogBody}>
        {pictureArr.length == 0 ? <Typography variant="h1" className={classes.heading} >Please add images </Typography> :
          <Grid container>
            <Grid item  xs={12} >
              <div className={classes.listingBox} >
                <GridList cellHeight={160} className={classes.gridList} cols={3}>
                {pictureArr.map((tile,index) => (
                  
                  <GridListTile key={index} cols={tile.cols || 1} 
                  onClick={()=>{
                    forcePreview(index);
                  }}
                  >
                    <img src={tile} />
                    <GridListTileBar 
                  actionIcon={
                    <IconButton className={classes.icon}
                    onClick={()=>{
                      forceDownload(index)
                    }}
                    >
                        <GetAppRounded />
                    </IconButton>
                  }
                  >
                  </GridListTileBar>
                  </GridListTile>
                ))}
              </GridList> 
              </div> 
            </Grid>
          </Grid>}
      </div>
      </Dialog>
      <canvas className={classes.canvas} ref={canvas}></canvas>
      <div className={classes.videoBarContainer} >
      <video className={classes.videoBar} ref={player}  autoPlay ></video>
      </div>
      <div className={classes.bottomIconPallate}>
        <Grid container spacing={2} >
          <Grid item 
          onClick={captureImage}
          >
            <Camera fontSize="large" color="white" />
          </Grid>
          <Grid item
          onClick={setupTimer}   
          >
            <Badge badgeContent={currentTimer} color="secondary" children={<Timer fontSize="large" color="white" />} />
          </Grid>
          <Grid item
          onClick={handlefaceMode}   
          >
            <CachedRounded fontSize="large" color="white" />
          </Grid>
        </Grid>
      </div>
      <div className={classes.bottomIconLeftPallate} >
        <Fab color="secondary"
        onClick={handleClickOpen}
        >
          <Badge badgeContent={pictureArr.length} color="secondary" children={<PhotoLibraryIcon />} />
        </Fab>
    </div>
     { currentTimer ?  <div className={classes.countdown}>
          <Typography variant="h1" component="h2" gutterBottom>
            {currentTimer}
          </Typography>
     </div> :""}
    </div>
  );
}

export default App;
